import * as Realm from 'realm-web'
import Chart, { plugins } from 'chart.js/auto'
import annotationPlugin from 'chartjs-plugin-annotation'
import chartTrendline from 'chartjs-plugin-trendline'
import React from 'react'
import './App.css';
import './TextBox.scss'
import './Button.css'
import './Dropdown.css'

function App() {
  const [tag, setTag] = React.useState();
  const [popupEnable, setPopupEnable] = React.useState(false)
  const [user, setUser] = React.useState(null)
  const [message, setMessage] = React.useState('')

  const pros = [['Select Pro',''],['IBDW','IBDW#0'],['Leffen','LEFFEN#0'],['MANG0','MANG#0'],['HungryBox','HBOX#305'],['aMSa','AMSA#0'], ['Plup','PLUB#754'],['Kodorin','KOD#0'], ['Hax$','XX#02']]

  React.useEffect(() => {
    const keyDownHandler = event => {
      if (event.key === 'Enter') {
        event.preventDefault()
        goButtonClick()
      }
    }

    document.addEventListener('keydown', keyDownHandler)

    return () => {
      document.removeEventListener('keydown', keyDownHandler)
    }
  })

  async function getUserDataFromMongo() {
    const app = new Realm.App({ id: 'slippielotracker-ksklh' });
    const credentials = Realm.Credentials.anonymous();
    try {
      const user = await app.logIn(credentials);
      const result = await user.functions.getUserData(tag)
      setUser(result)
      if (result == null){
        setMessage('New user added to database, check back again after a few games.')
        user.functions.addNewUser(tag)
      }
      else {
        setMessage('')
        setPopupEnable(true)
        await new Promise(resolve => setTimeout(resolve, 100));
        makeChart(result)
      }
    } catch(err) {
      console.error(err);
    }
  }

  const makeChart = (result) => {
    var labels = []
    var rankValues = [766,914,1055,1189,1316,1436,1549,1654,1752,1843,1928,2004,2074,2137,2192,2275,2350]
    var rankWords = ['Bronze 2', 'Bronze 3', 'Silver 1', 'Silver 2', 'Silver 3', 'Gold 1', 'Gold 2', 'Gold 3', 'Platinum 1', 'Platinum 2', 'Platinum 3', 'Diamond 1', 'Diamond 2', 'Diamond 3', 'Master 1', 'Master 2', 'Master 3']
    var max = Math.max(...result.datapoints)
    var min = Math.min(...result.datapoints)

    let characterColorFade = ['#75cc85', '#63c78a', '#51c28f', '#3dbc94', '#24b799', '#00b19d', '#00aba0', '#00a5a2', '#009ea4', '#0098a5', '#0091a5', '#008ba4', '#0084a1', '#007d9e', '#00769a', '#166f95', '#256890', '#2f6189', '#375b82', '#3c547b', '#404e72', '#43476a', '#444162', '#443c59', '#433650', '#413148']

    var characterNames = []
    var characterGames = []
    for (let i = 0; i < result.characters.length; i++){
      characterNames.push(result.characters[i].character)
      characterGames.push(result.characters[i].gameCount)
    }

    var rankLines = []
    for (let i = 0; i < rankValues.length; i++){
      if (rankValues[i] > min-150 && rankValues[i] < max+150) {
        rankLines.push([rankValues[i], rankWords[i]])
      }
    }

    var numberOfDatapoints = result.datapoints.length

    for (let i = 0; i < numberOfDatapoints; i++){
      labels.push(String(i))
    }

    Chart.register(annotationPlugin)
    Chart.register(chartTrendline)

    const characterDistChart = new Chart('characterChart', {
      type: 'pie',
      data: {
        labels: characterNames,
        datasets: [{
          label: 'Character Games',
          data: characterGames,
          backgroundColor: characterColorFade
        }]
      },
      options: {
        aspectRatio: 2
      }
    })
    if (characterNames.length > 6) {
      characterDistChart.config.type = 'radar'
      characterDistChart.update()
    }


    new Chart('winsVLossesChart', {
      type: 'pie',
      data: {
        labels: ['Wins', 'Losses'],
        datasets: [{
          label: 'Wins and Losses',
          data: [result.wins, result.losses],
          backgroundColor: ['#75cc85', '#413148']
        }]
      },
      options: {
        aspectRatio: 2
      }
    })

    var lineChart = null

    if (numberOfDatapoints > 1) {
      lineChart = new Chart('rankChart', {
        type: 'line',
        data: {
          labels: labels,
          datasets: [{
            tension: 0.2,
            pointRadius: 4,
            label: result.tag,
            data: result.datapoints,
            backgroundColor: '#413148',
            borderColor: '#75cc85',
            borderWidth: 5,
            trendlineLinear: {
              colorMax: '#413148',
              colorMin: '#413148',
              lineStyle: "solid",
              width: 3
            }
          }]
        },
        options: {
          responsive: true,
          aspectRatio: 2,
          plugins: {
            annotation: {
              annotations: {}
            }
          }
        }
      })
    }
    else {
      lineChart = new Chart('rankChart', {
        type: 'line',
        data: {
          labels: labels,
          datasets: [{
            tension: 0.2,
            pointRadius: 4,
            label: result.tag,
            data: result.datapoints,
            backgroundColor: '#413148',
            borderColor: '#75cc85',
            borderWidth: 5
          }]
        },
        options: {
          responsive: true,
          aspectRatio: 2,
          plugins: {
            annotation: {
              annotations: {}
            }
          }
        }
      })
    }  

    for (let i = 0; i < rankLines.length; i++) {
      lineChart.options.plugins.annotation.annotations['lineAnnotation' + String(i)] = {
        type: 'line',
        yMin: rankLines[i][0],
        yMax: rankLines[i][0],
        borderWidth: 1,
        borderColor: '#111111',
      }
      lineChart.options.plugins.annotation.annotations['labelAnnotation' + String(i)] = {
        type: 'label',
        yValue: rankLines[i][0]+5,
        xValue: 1,
        borderWidth: 0,
        content: rankLines[i][1]
      }
    }
    lineChart.update()
  }

  function verifyTag(tag) {
    const re = /^[A-Z0-9]{1,6}#[0-9]{1,3}$/
    return re.test(tag)
  }

  const tagChangeHandler = (e) => {    
    const tag = e.target.value.toUpperCase();
    setTag(tag)
  };

  const goButtonClick = () => {
    if (verifyTag(tag)) {
      setMessage('')
      getUserDataFromMongo()
    }
    else {
      setMessage('This is not a valid tag. Try again following the format.')
    }
  }

  const closeButtonClick = (e) => {
    setMessage('')
    setPopupEnable(false)
  }

  return (
    <div className='App'>
      {!popupEnable && 
        <div>
        <input class='tagInput' maxLength='8' spellCheck='false' type='text' placeholder='user#1234' value={tag} onChange={tagChangeHandler}/>
        <br/>
        <button class='tagButton' role='button' onClick={goButtonClick}>Go</button>
        <br/>
        <br/>
        <div class='box'>
          <select class='proDrop' onChange={tagChangeHandler}>
            {pros.map((pro) => {
              return <option value={pro[1]}>{pro[0]}</option>
            })
            }
          </select>
        </div>
        {message.length > 1 && 
          <div>
          <label>{message}</label>
          </div>
        }
        </div>
      }
      {popupEnable && 
        <div>
        
        {message.length > 1 && 
          <div>
          <label>{message}</label>
          </div>
        }
        {message.length < 1 && 
          <div class='charts'>
            <div class='mainChart'>
              <canvas id="rankChart"></canvas>
            </div>
            <div class='sideCharts'>
              <canvas id="characterChart"></canvas>
              <canvas id="winsVLossesChart"></canvas>
            </div>
          </div>
        }
        <br/>
        <button class="tagButton" role="button" onClick={closeButtonClick}>Go Back</button>
        </div>
      }
    </div>
  );
}

export default App;
